.Nav {
  position: absolute;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 4rem;
  color: inherit;
}

.Nav-items {
  margin: 0;
  padding: 0;
  width: inherit;
  height: inherit;
  list-style: none;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;  
}

.Nav-items > li:not(:last-child) {
  margin-right: 1.5rem;
}

.Nav-item {
  margin: 0;
  padding: 0;
  padding: 0.4rem;
  text-align: center;
  text-decoration: none;
  font-size: var(--nav-font-size);
  letter-spacing: var(--main-letter-space);
  color: inherit;
}

.Nav-item:hover {
  box-shadow: 0 2px 0 0 var(--shadow-color);
  transition: box-shadow 0.4s;
}

.nav-active {
  color: rgb(240, 240, 240);
  background-color: var(--main-font-color);
  transition: all 0.4s;
}

.Nav-icon:hover {
  color: var(--active-font-color);
  transition: color 0.4s;
}

.Nav-responsive {
  display: none;

  margin: 0;
  padding: 0;
  font-size: var(--nav-font-size);
  letter-spacing: var(--main-letter-space);
  color: inherit;
}

@media screen and (max-width: 1199px) {
  .Nav-items {
    display: none;
  }

  .Nav-responsive {
    height: 4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;

  }

  .Nav-icon {
    margin-right: 1.5rem;
  }
}

.Home {
  height: calc(100vh - 4rem);
  margin: 0;
  padding: 0;
  padding-top: 4rem;
  color: inherit;
  background-image: linear-gradient(to bottom right, rgb(242, 242, 242), rgb(200, 200, 200));
  grid-column-start: span 3;
  
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-areas: "image content";
  grid-column-gap: 8rem;
  -webkit-column-gap: 8rem;
          column-gap: 8rem;
}

.Home-image {
  grid-area: image;
  align-self: center;
  justify-self: end;
  height: 500px;
  width: 500px;
}

.Home-content {
  max-width: 500px;
  grid-area: content;
  align-self: center;
  justify-self: start;
}

.Home-title {
  margin: 0;
  padding: 0;
  font-size: var(--home-title-font-size);
  font-weight: 700;
  letter-spacing: 0.4rem;
  text-shadow: 4px 4px 6px var(--shadow-color);
}

.Home-description {
  margin-top: 2rem;
}

.Home-description > p {
  margin: 0;
  margin-bottom: 1rem;
  padding: 0;
  font-size: var(--home-description-font-size);
  font-weight: 400;
}

.Home-description > a:not(:last-child) {
  margin-right: 1rem;
}

.Home-icon:hover {
  color: var(--active-font-color);
  transition: color 0.4s;
}

@media screen and (max-width: 1199px) {
  .Home {
    grid-template-rows: repeat(2, 1fr);
    grid-template-columns: 1fr;
    grid-template-areas: 
    "content"
    "image";

    grid-column-gap: 0;
  }

  .Home-content {
    max-width: 375px;
    align-self: center;
    justify-self: center;
  }

  .Home-image {
    height: 325px;
    width: 325px;
    align-self: center;
    justify-self: center;
  }

  .Home-title {
    font-size: 2.4rem;
    font-weight: 800;
    text-shadow: none;
  }

  .Home-description {
    margin-top: 1.5rem;
  }

  .Home-description > p {
    font-size: var(--main-font-size);
  }
}

@media screen and (max-width: 799px) {
  .Home-content {
    max-width: 300px;
    align-self: center;
    justify-self: center;
  }

  .Home-image {
    height: 275px;
    width: 275px;
    align-self: center;
    justify-self: center;
  }

  .Home-title {
    font-size: 1.8rem;
    font-weight: 800;
    text-shadow: none;
  }

  .Home-description {
    margin-top: 1rem;
  }

  .Home-description > p {
    font-size: var(--min-font-size);
  }
}

