.Menu {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  position: fixed;
  z-index: 300;
  background-color: var(--main-font-color);
  color: rgb(240, 240, 240);

  -webkit-transform: translateY(-200%);

          transform: translateY(-200%);
  transition: -webkit-transform 0.5s ease-in-out;
  transition: transform 0.5s ease-in-out;
  transition: transform 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;
}

.Menu-content {
  margin: 0;
  padding: 0;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.Menu-open {
  -webkit-transform: translateY(0%);
          transform: translateY(0%);
}

.Menu-close {
  width: 100%;
  margin: 0;
  padding: 0;
  padding-top: 2rem;
  padding-bottom: 2rem;
  font-size: var(--nav-font-size);
  letter-spacing: var(--main-letter-space);
  cursor: pointer;

  display: flex;
  align-items: center;
  justify-content: center;
}

.Menu-close > svg {
  margin-right: 1.5rem;
}

.Menu-items {
  width: 100%;
  margin: 0;
  margin-top: 1rem;
  padding: 0;
  list-style: none;
  text-align: center;

  display: flex;
  flex-direction: column;
}

.Menu-items li {
  width: 100%;
  margin-bottom: 1rem;
}

.Menu-item {
  display: block;
  width: 100%;
  margin: 0;
  padding: 0;
  padding-top: 1rem;
  padding-bottom: 1rem;
  font-size: var(--nav-font-size);
  letter-spacing: var(--main-letter-space);
  text-decoration: none;
  color: inherit;
}

.Menu-item:hover,
.Menu-item:active {
  color: var(--active-font-color);
}

.menu-active {
  font-weight: 600;
  color: var(--main-font-color);
  background-color: var(--shadow-color);
}

